.card__checkbox:checked ~ .card__icon--full {
  transform: scale(1.2);
  transition: transform 0.3s ease-in-out, color 0.3s ease-in-out;
  opacity: 1;
}

.card__checkbox:checked ~ .card__icon--empty {
  opacity: 0;
}

.card__checkbox:not(:checked) ~ .card__icon--full {
  opacity: 0;
}

.card__checkbox:not(:checked) ~ .card__icon--empty {
  opacity: 1;
}

.menu__item {
  position: relative;
  overflow: hidden;
}

.menu__check {
  position: absolute;
  top: 0;
  right: 0;
  width: 59px;
  height: 100%;
  background-color: #99E2D0;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  transform: translateX(100%);
  transition: transform 0.5s ease-out;
  z-index: 3;
}

.menu__check-circle {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}

.menu__check-icon {
  color: #99E2D0;
  font-size: 12px;
  transition: transform 0.5s ease-out;
}

.menu__check-input {
  display: none;
}

.menu__check-input:checked ~ .menu__content .menu__price {
  transform: translateX(-59px);
  transition: transform 0.5s ease-out;
}

.menu__check-input:checked + .menu__check {
  transform: translateX(0);
}

.menu__check-input:checked + .menu__check .menu__check-circle .menu__check-icon {
  transform: rotate(360deg);
}

.loader-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: white;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  z-index: 1000;
  animation: fadeOut 1s ease-out 3s forwards;
}

.loader {
  width: 60px;
  height: 60px;
  border: 6px solid transparent;
  border-top: 6px solid #9356DC;
  border-right: 6px solid #FF79DA;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

.loader-text {
  margin-top: 15px;
  font-size: 1.5rem;
  font-family: "Shrikhand", sans-serif;
  color: black;
  animation: pulse 1.5s ease-in-out infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes pulse {
  0%, 100% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.1);
    opacity: 0.8;
  }
}
@keyframes fadeOut {
  0% {
    opacity: 1;
    visibility: visible;
  }
  100% {
    opacity: 0;
    visibility: hidden;
  }
}
.menu__section {
  margin-top: 42px;
  /* 2.625rem */
  padding: 13px;
}
.menu__section h2 {
  margin: 0 0 42px 0;
  /* ou 2.625rem en bas */
  position: relative;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 300;
  line-height: 18.75px;
  letter-spacing: 0;
}
.menu__section h2::after {
  content: "";
  width: 40px;
  /* largeur de la petite ligne */
  border-top: 3px solid #99E2D0;
  position: absolute;
  left: 0;
  top: 100%;
  margin-top: 10px;
  /* l'espace entre le texte et la ligne */
  display: block;
}

/* ================================
   3) UN ITEM DE MENU : .menu__item
================================ */
.menu__item {
  position: relative;
  width: 100%;
  min-height: 69px;
  margin-bottom: 12px;
  padding: 12px 0;
  border-radius: 15px;
  box-shadow: 0px 4.01px 15.04px rgba(0, 0, 0, 0.1);
  display: flex;
  /* S√©paration horizontale : texte et barre verte */
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  overflow: hidden;
  /* Pour masquer la barre quand elle sort du cadre */
  cursor: pointer;
  transition: background-color 0.3s, box-shadow 0.3s;
  /* Au survol, petit effet visuel */
}
.menu__item:last-child {
  margin-bottom: 0;
}
.menu__item:hover {
  background-color: #f2f2f2;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

/* ================================
   4) LE CONTENU (texte + prix)
================================ */
.menu__content {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding-right: 15px;
  box-sizing: border-box;
  position: relative;
  z-index: 2;
  overflow: hidden;
}

/* Le bloc texte (titre + description) */
.menu__text {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: 5px;
  padding-left: 15px;
  width: 287px;
  box-sizing: border-box;
  flex-grow: 1;
  flex-shrink: 1;
  min-width: 0;
  /* Permet le text-overflow si la fen√™tre est √©troite */
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

/* Titre / Description */
.menu__title {
  font-family: Roboto, sans-serif;
  font-size: 18px;
  font-weight: 500;
  line-height: 21px;
  margin: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.menu__description {
  font-family: Roboto, sans-serif;
  font-size: 15.04px;
  font-weight: 300;
  line-height: 17.63px;
  margin: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* Le prix, qu'on peut pousser √† gauche en anim */
.menu__price {
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 500;
  margin-left: auto;
  white-space: nowrap;
  transition: transform 0.5s ease-out;
}

/* ================================
   5) LA BARRE VERTE : .menu__check
================================ */
.menu__check {
  position: absolute;
  top: 0;
  right: 0;
  width: 59px;
  height: 100%;
  background-color: #99E2D0;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  transform: translateX(100%);
  /* cach√©e hors du cadre */
  transition: transform 0.5s ease-out;
  z-index: 3;
  overflow: hidden;
  /* Le cercle blanc */
  /* L'ic√¥ne check */
}
.menu__check .menu__check-circle {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}
.menu__check .menu__check-icon {
  color: #99E2D0;
  font-size: 12px;
  opacity: 0;
  /* on fera un fadeIn via keyframes, ou un simple transition */
}

/* ================================
   6) CHECKBOX (cach√©) : d√©clencheur
================================ */
.menu__check-input {
  display: none;
}

/* ================================
   7) AU CLIC (checkbox :checked)
      => la barre verte glisse,
         le prix recule,
         l‚Äôic√¥ne fadeIn + spin
================================ */
.menu__check-input:checked ~ .menu__content .menu__price {
  transform: translateX(-59px);
}

.menu__check-input:checked + .menu__check {
  transform: translateX(0);
  animation: fadeIn 0.3s forwards;
  /* Barre verte fade + slide */
}

/* L'ic√¥ne check : fadeIn + spin (0.5s) */
.menu__check-input:checked + .menu__check .menu__check-circle .menu__check-icon {
  animation: fadeIn 0.3s forwards, spin 0.5s ease-out forwards;
  opacity: 1;
}

